<template>
  <div class="page payment-page" id="pageDetail">
    <mt-header class="bar-nav" title="" >
      <mt-button slot="left" icon="back" @click.native="goBack"></mt-button>
    </mt-header>
    <div class="form-invest">
      <div v-if="resdata.saleStyle==1" class="form-group margin-t-20 margin-b-10">
        <label></label>
        <span class="main-color">{{ resdata.remainAccount | currency('',2)}}</span>
      </div>
      <div v-if="resdata.saleStyle==2" class="form-group margin-t-20 margin-b-10">
        <label></label>
        <span class="main-color">{{ resdata.totalCopies}}</span>
      </div>
      <div class="form-group margin-b-20">
        <label></label>
        <span v-if="resdata.saleStyle==1" class="color-333">{{ resdata.lowestAccount | currency('',2)}} - {{ userCanInvest | currency('',2)}}</span>
        <span v-if="resdata.saleStyle==2" class="color-333">{{ resdata.lowestCopies}} - {{ userCanInvest}}</span>
      </div>
      <div v-if="resdata.saleStyle==1" class="form-list">
        <label>()</label>
        <input ref="inputMoney" type="number" v-model.trim="investMoney" class="invest-money" id="invest-money" placeholder="" />
      </div>
      <div v-if="resdata.saleStyle==2" class="form-list">
        <label>()</label>
        <input type="tel" name="" v-model.trim="investMoney" class="invest-money"  :placeholder="''+resdata.copyAccount+''" />
      </div>
      <div class="form-group margin-b-20 earnings">
        <label style="width: 20%;"></label>
        <span class="main-color">{{ interest }}</span>
      </div>
      <ul class="form-group-select">
        <li v-if="resdata.redEnvelopeUseful == 0" class="form-red aui-border-b border-color disabled">
          <label></label>
          <span class="margin-r-20"></span>
          <img src="../../../assets/images/public/arrow_right.png" class="right-go"/>
        </li>
        <li v-else class="form-red aui-border-b border-color" @click="selectCoupon('investRedpacket')">
          <label></label>
          <span class="margin-r-20">{{ redpacketNum }}</span>
          <img src="../../../assets/images/public/arrow_right.png" class="right-go"/>
        </li>
        <li v-if="resdata.additionalRateUseful == 0" class="form-coupon aui-border-b border-color disabled">
          <label></label>
          <span class="margin-r-20"></span>
          <img src="../../../assets/images/public/arrow_right.png" class="right-go"/>
        </li>
        <li v-else class="form-coupon aui-border-b border-color" @click="selectCoupon('investCoupon')">
          <label></label>
          <span class="margin-r-20">{{selected_upApr}}</span>
          <img src="../../../assets/images/public/arrow_right.png" class="right-go"/>
        </li>
        <li class="form-coupon">
          <label></label>
          <span>{{ resdata.userMoney | currency('',2)}}</span>
        </li>
      </ul>
      <div class="operator">
        <mt-button v-show="!investMoney" size="large" type="default" disabled> 0.00</mt-button>
        <mt-button v-show="investMoney" size="large" type="danger" @click.native.prevent="submitForm">
          <span class="font-arial">{{ actualInvestMoney | currency('',2) }}</span>
        </mt-button>
        <loading v-if="false" ></loading>
      </div>
      <p class="tips">
        <i class="main-color"></i><i class="main-color">{{ resdata.orderEnableTime }}</i>
      </p>
    </div>
  </div>
</template>
<script>
  import * as ajaxUrl from '../../../ajax.config'
  import Loading from '../../../components/Loading.vue'
  import qs from 'qs'
  export default {
    name: 'investBid',
    data(){
      return {
        investMoney: sessionStorage.investMoney,
        resdata: '',
        userCanInvest: '',
        urlParams: {
          projectId: this.$route.params.projectId,
          userId: this.$store.state.user.userId,
          __sid: this.$store.state.user.__sid
        },
        interest: '0.00',
        redpacketNum: this.$store.state.redpack.selected_total + '',
        selected_upApr: sessionStorage.selected_upApr + '%'
      }
    },
    computed: {
      actualInvestMoney(){
        let red_num = parseInt(this.redpacketNum)
        if(this.resdata.saleStyle == 2) { //
          if(!isNaN(red_num)){
            return this.investMoney*this.resdata.copyAccount - red_num
          }else{
            return this.investMoney*this.resdata.copyAccount
          }
        }else{
          if(!isNaN(red_num)){
            return this.investMoney - red_num
          }else{
            return this.investMoney
          }
        }
      }
    },
    created(){
      if(this.$route.query.from){  //
        this.submitUrl = ajaxUrl.doRealizeInvest
      }
      if(this.investMoney == undefined || this.investMoney == ''){
        this.redpacketNum = ''
        this.selected_upApr = ''
      }
      if(this.$store.state.redpack.selected_total == 0){this.redpacketNum = ''}
      if(sessionStorage.selected_upApr == 0){this.selected_upApr = ''}
      let getParams = Object.assign({investType: 'invest'}, this.urlParams)
      this.$http.get(ajaxUrl.initInvest, {params:getParams}).then((res) => {
        this.resdata = res.data.resData
        this.userCanInvest = this.resdata.userCanInvestAmount
        if(this.resdata.saleStyle == 2){  // 
          this.userCanInvest = this.resdata.userCanInvestAmount/this.resdata.copyAccount
        }
        if(this.resdata == ''){
          this.$messagebox({
            title: ' ',
            cancelButtonText: '',
            confirmButtonText: '',
            showCancelButton: true,
            message: res.data.resMsg
          }).then(action => {
            if(action == 'cancel'){
              this.goBack()
            }else if(action == 'confirm'){

            }
          });
        }
        if(this.investMoney != ''){ //
          this.investInterest(this.investMoney)
        }
      })

    },
    watch: {
      investMoney (newV, oldV) {
        if(newV){
          if(newV.toString().length > 7){
            this.investMoney = oldV
          }
          if(newV > this.userCanInvest){
            if(this.resdata.saleStyle == 2){
              this.$toast(''+ this.userCanInvest +'')
            }else{
              this.$toast(''+ this.userCanInvest +'')
            }
            this.investMoney = this.userCanInvest
          }
          sessionStorage.investMoney = newV
          this.investInterest(newV)
          this.redpacketNum = ''
          this.$store.dispatch('selectRedpacket', [])
          this.$store.dispatch('selectRedpacketTotal', 0)
          this.selected_upApr = ''
          sessionStorage.selected_coupon = ''
          sessionStorage.selected_upApr = '0.00'
        }else{
          sessionStorage.investMoney = ''
          this.interest = '0.00'
        }
      }
    },
    methods: {
      // 
      goBack(){
        sessionStorage.investMoney = ''
        this.$router.go(-1)
      },
      //
      investInterest(money, upApr=0){
        if(this.resdata.saleStyle == 2){
          money = money * this.resdata.copyAccount
        }
        let reqParams = Object.assign({amount: money, upApr: upApr}, this.urlParams)
        this.$http.post(ajaxUrl.interest, qs.stringify(reqParams)).then((res) => {
          this.interest = res.data.resData.interest
        })
      },
      // 
      selectCoupon(arg){
        if(!this.investMoney){
          let str = this.resdata.saleStyle == 2 ? '' : ''
          this.$toast(str)
        }
        else{
          //
          let limit_num = this.resdata.redEnvelopeRate * Number(this.investMoney)/100
          if(this.resdata.saleStyle == 2){
            limit_num = limit_num*this.resdata.copyAccount
          }
          sessionStorage.redpack_max_limit = limit_num
          this.$router.push({name: arg, query:{copyAccount:this.resdata.copyAccount}})
        }
      },
      submitForm(){
        let amount = this.resdata.saleStyle == 2 ? this.investMoney*this.resdata.copyAccount : this.investMoney;
        if(amount > this.resdata.userMoney){
          this.$messagebox({
            title: ' ',
            cancelButtonText: '',
            confirmButtonText: '',
            showCancelButton: true,
            message: ''
          }).then(action => {
            if(action == 'cancel'){
              this.investMoney = ''
              this.$refs.inputMoney.focus()
            }
          });
          return ;
        }
        let rateCouponId = sessionStorage.selected_coupon
        let redEnvelopeIds = this.$store.state.redpack.selectedList.join(',')
        let params = {
          amount: amount,
          redEnvelopeIds: redEnvelopeIds,
          rateCouponId:rateCouponId,
          investToken: this.resdata.investToken,
          projectId: this.$route.params.projectId,
          userId: this.$store.state.user.userId,
          __sid: this.$store.state.user.__sid,
        }
        this.$http.post(ajaxUrl.doInvest, qs.stringify(params)).then((res) => {
          if(res.data.resData == ''){ //
            this.$toast(res.data.resMsg)
          }else{
            document.write(res.data)
            sessionStorage.investMoney = ''
            sessionStorage.investBidName = this.resdata.projectName
            sessionStorage.investBidMoney = amount
          }
        })
      }
    },
    components: { Loading }
  }
</script>
<style scoped>
  .disabled {color:#999;}
  .form-group-select li.disabled span {color:#999;}
  .form-invest{
    width: 100%;
  }
  .form-group{
    line-height: 1;
    float: left;
    width: 100%;
    padding: 0 5%;
  }
  .form-group label{
    color: #666;
    display: inline-block;
    width: 30%;
    float: left;
  }
  .form-group span{
    display: inline-block;
    float: left;
    width: 70%;
    font-family: sarial;
  }
  .form-list{
    float: left;
    width: 100%;
    background: #fff;
    padding: 0 5%;
    height: .44rem;
    line-height: .44rem;
  }
  .form-list label{
    display: inline-block;
    float: left;
    width: 30%;
  }
  .form-list input{
    width: 70%;
    border: none;
    outline: none;
    float: left;
    height: .3rem;
    line-height: .3rem;
    margin-top: .07rem;
  }
  .earnings{
    margin: .13rem 0;
  }
  .form-group-select{
    width: 100%;
    padding: 0 5%;
    background: #fff;
    float: left;
  }
  .form-group-select li{
    height: .48rem;
    line-height: .48rem;
  }
  .form-group-select li span{
    float: right;
    font-family: arial;
    color: #666;
  }
  .right-go{
    position: absolute;
    right: 0;
    top: .17rem;
    height: .14rem;
    width: .14rem;
  }
  .operator{
    float: left;
    width: 100%;
    padding: .3rem .15rem .15rem;
  }
  .tips{
    float: left;
    width: 100%;
    padding: 0 5%;
    font-size: .12rem;
    color: #999;
    line-height: .2rem;
  }
</style>



